#startViewContainer {
    width: 100%;
    height: 100%;

    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;
}

#startViewContainer #multipleLogos {
    padding: 1vw 0 1vw 0;

    background: linear-gradient(135deg, rgba(255, 255, 255, 0.1), rgba(255, 255, 255, 0));
    backdrop-filter: blur(10px);
    -webkit-backdrop-filter: blur(10px);
    border-radius: 50vw;
    border:1px solid rgba(255, 255, 255, 0.18);
    box-shadow: 0 8px 32px 0 rgba(0, 0, 0, 0.37);

    width: 50%;
    margin-top: 50px;
    display: flex;
    justify-content: space-around;
    align-items: center;
    flex-direction: row;
}

#startViewContainer #multipleLogos div {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}

#startViewContainer #multipleLogos img {
    width: 3.5vw;
}

#startViewContainer #multipleLogos div p {
    color: white;
    font-family: Avenir-Regular;
    font-size: 1vw;
    margin-top: 7px;
}

#pictureOfMe {
    margin-bottom: 20px;

    width: 15vw;
    height: 15vw;
    object-fit: cover;

    border-radius: 15vw;

    border: 5px solid white;
}

#myLogoName {
    color: white;
    font-family: Avenir-Bold;
    font-size: 2.5vw;
}

#logoDesc {
    color: white;
    font-family: Avenir-Regular;
    font-size: 1.5vw;
    margin: 0;
}

#logoDesc span {
    font-style: italic;
}

@media only screen and (max-width: 800px) {
    #pictureOfMe {
        width: 20vh;
        height: 20vh;
        border-radius: 15vh;
    }

    #myLogoName {
        font-size: 4vh;
    }

    #logoDesc {
        font-size: 2.5vh;
    }

    #startViewContainer #multipleLogos {
        padding: 2vh 1vh 2vh 1vh;
        width: 80%;
    }

    #startViewContainer #multipleLogos img {
        margin: 0 auto;
        width: 3.5vh;
    }

    #startViewContainer #multipleLogos div p {
        font-size: 2vh;
    }
}