@keyframes PopIn {
    from {
        transform: translateY(6vh);
        opacity: 0;
    }
    to {
        transform: translateY(0vh);
        opacity: 1;
    }
}


#scrollDownPopupContainer {
    position: absolute;
    bottom: 0;

    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;

    padding: 15px;
}

#scrollDownPopupContainer p {
    color: white;
    font-size: 1.5vw;
    font-family: Avenir-Light;

    animation-name: PopIn;
    animation-duration: 1s;
}

#scrollDownIcon {
    color: white;
    font-size: 1.5vw;
}

@media only screen and (max-width: 800px) {
    #scrollDownPopupContainer p {
        font-size: 2.5vh;
    }

    #scrollDownIcon {
        font-size: 2.5vh;
    }

    #scrollDownPopupContainer {
        display: none;
    }
}