#aboutViewContainer {
    width: 100vw;
    height: 100vh;

    margin: 0;
}

#descSectionContainer {
    width: 50%;
    height: 100vh;

    margin: 0;
    float: right;

    display: flex;
    justify-content: center;
    align-items: center;

    flex-direction: column;
}

#mySkillsSectionContainer {
    width: 50%;
    height: 100vh;

    margin: 0;
    float: left;

    display: flex;
    justify-content: center;
    align-items: center;
}

#mySkillsContainer {
    width: 100%;
    height: auto;

    border-radius: 20px;

    background-color: #F5F5F5;

    padding: 15px;

    margin: 0 2vw 0 2vw;

    box-shadow: 0 8px 32px 0 rgba(0, 0, 0, 0.30);
}

#mySkillsContainer h2 {
    margin: 0;
    font-family: Avenir-Bold;
    font-size: 2vw;
    color: #262626;
}

#aboutMeContainer {
    background-color: white;
    margin: 0 2vw 0 2vw;
    border-radius: 20px;

    background: linear-gradient(135deg, rgba(255, 255, 255, 0.1), rgba(255, 255, 255, 0));
    backdrop-filter: blur(10px);
    -webkit-backdrop-filter: blur(10px);
    border:1px solid rgba(255, 255, 255, 0.18);
    box-shadow: 0 8px 32px 0 rgba(0, 0, 0, 0.37);

    padding: 15px;
}

#aboutMeContainer h1 {
    margin: 0;
    color: white;
    font-family: 'Avenir-Bold';
    font-size: 2.25vw;
}

#aboutMeContainer p {
    color: white;
    font-family: 'Avenir-Regular';
    font-size: 1.25vw;
}

#contactMeContainer {
    margin-top: 50px;
}

#contactMeContainer h1 {
    color: white;
    font-family: 'Avenir-Bold';
    font-size: 2vw;
}

@media only screen and (max-width: 800px) {
    #aboutViewContainer {
        height: auto;

        display: flex;
        justify-content: space-evenly;
        align-items: center;
        flex-direction: column;
    }

    #descSectionContainer, #mySkillsSectionContainer {
        width: 85%;
        height: auto;
    }

    #descSectionContainer {
        margin-top: 30px;
    }

    #mySkillsContainer h2 {
        font-size: 2.5vh;
    }

    #aboutMeContainer h1 {
        font-size: 3vh;
    }

    #aboutMeContainer p {
        font-size: 2vh;
    }

    #contactMeContainer h1 {
        font-size: 3vh;
    }
}