.contactButton {
    margin-top: 10px;

    cursor: pointer;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    background: transparent;
    border: 0
}

.contactButton img {
    width: 2.25vw;
    height: 2.25vw;
}

.contactButton p {
    color: white;
    font-size: 1vw;
    font-family: Avenir-Light;
    margin-top: 10px;
}

@media only screen and (max-width: 800px) {
    .contactButton img {
        width: 4.25vh;
        height: 4.25vh;
    }

    .contactButton p {
        font-size: 2.5vh;
    }
}