@keyframes GrowingBar {
    from {
        width: 0;
    }
    to {
        width: 100%;
    }
}

.growing-bar {
    animation: GrowingBar;
    animation-duration: 2s;
}

.animatedSkillContainer img {
    width: 3vw;
    height: 3vw;
}

@media only screen and (max-width: 800px) {
    .animatedSkillContainer img {
        width: 5vh;
        height: 5vh;
    }
}